@each $color, $value in $project-colors {
    .text-#{$color} {
        color: $value;
    }

    .bg-#{$color} {
        background-color: $value;
    }
}

.bg-gray-100 {
    background-color: var(--gray-100, $gray-100);
}

.text-white {
    a:not(.btn) {
        &,
        &:hover,
        &:focus,
        &:active {
            color: inherit;
        }
    }
}

a[class*="bg"] {
    display: block;
    text-decoration: none;

    &:hover {
        text-decoration: none;
        color: inherit;
    }
}