.wysiwyg,
.cke_editable {
    ul, ol, dl {
        margin: 0 0 $paragraph-margin-bottom 1.5rem;

        ul, ol, dl {
            margin-top: 1rem;
            margin-bottom: 1rem;
        }
    }

    ul {
        @include list-ul();
    }

    ol {
        @include list-ol();
    }

    dd {
        margin-bottom: 2rem;
        margin-left: 2rem;
    }

    li {
        overflow: auto; // fix list marker position when there is a floating element next to a list
    }

    :last-child {
        margin-bottom: 0;
    }
}