.header-principal__nav {
    .navbar-toggler {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
        border: 0;
    }

    .nav-item,
    .collapse-wrapper {
        position: relative; // needed to position collapse menu
    }

    .navbar-nav {
        .nav-title {
            display: block;
            padding-top: 1rem;
            font-weight: $fw-semiBold;
            color: var(--body-color-darker);
        }

        .nav-link {
            display: block;
            width: 100%;
            padding: .8rem;
            line-height: 1.3;
            text-align: left;
            transition: color 250ms ease;

            &:hover,
            &:focus,
            &:active {
                color: var(--primary);
            }
        }

        [aria-expanded] {
            position: relative;
            padding-right: 3rem;

            @include arrow(after) {
                position: absolute;
                top: 3rem;
                right: 1rem;
                transition: transform 250ms ease;
            }
        }

        [aria-expanded="true"] {
            &::after {
                transform: rotate(-180deg);
            }
        }

        .is-active > .nav-link {
            color: var(--primary);
        }
    }

    // Lvl 1
    .nav-lvl--1 {
        > li {
            > .nav-link {
                padding-top: 2rem;
                padding-bottom: 2rem;
                font-weight: $fw-medium;
            }
        }
    }

    // Lvl 2 and more
    .navbar-nav:not(.nav-lvl--1) {
        display: block;

        .nav-link {
            position: relative;
            padding-left: 1.8rem;

            @include arrow() {
                position: absolute;
                top: 1.7rem;
                left: 0;
                transform: rotate(-90deg);
            }
        }
    }

    .nav-right {
        display: flex;
        align-items: center;
        margin: 0;
    }

    .nav-collapse {
        position: absolute;
        top: 100%;
        left: 0;
        min-width: 28.5rem;
        padding: 1rem 3rem;
        background-color: $white;
        border: 0.1rem solid $border-color;
        border-radius: 0 var(--border-radius-sm) var(--border-radius-sm) var(--border-radius-sm);
        box-shadow: var(--box-shadow);
    }

    .nav-collapse--right {
        right: 0;
        left: auto;
        margin-top: 0.9rem;
        padding: 2.5rem;
        border-radius: var(--border-radius-sm) 0 var(--border-radius-sm) var(--border-radius-sm);
    }

    @include media-breakpoint-down(md) {
        .navbar {
            position: static;
        }

        .navbar-collapse {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            padding-top: 1.5rem;
            padding-bottom: 6.2rem;
            z-index: $zindex-fixed;
            overflow-y: auto;
            background-color: $white;
        }

        // Lvl 1
        .nav-lvl--1 {
            padding: 0 2.8rem;

            > li {
                border-bottom: 0.1rem solid $border-color;
            }

            .nav-collapse {
                position: static;
                padding: 0;
                border: 0;
                box-shadow: none;
            }
        }

        // Lvl 2
        .nav-lvl--2 {
            margin-bottom: 1.5rem;
            margin-left: 1.5rem;
        }

        .nav-right {
            position: fixed;
            left: 0;
            bottom: 0;
            width: 100%;
            flex-wrap: wrap;
            background-color: $white;
            border-top: 0.1rem solid $border-color;
            z-index: $zindex-fixed + 1;

            li {
                @include aw-make-col-value(50%);
                padding: 0.5rem 0;
                text-align: center;
                border-bottom: 0.1rem solid $border-color;

                &:nth-child(even) {
                    border-left: 0.1rem solid $border-color;
                }
            }

            .nav-collapse {
                bottom: 100%;
                top: auto;
                left: 1rem;
                right: auto;

                margin-bottom: 1rem;
                border-radius: var(--border-radius-sm) var(--border-radius-sm) var(--border-radius-sm) 0;
            }
        }
    }

    @include media-breakpoint-up(lg) {
        // Lvl 2 and more
        .navbar-nav:not(.nav-lvl--1) {
            .nav-link {
                padding-top: 1.6rem;
                padding-bottom: 1.6rem;

                &::before {
                    top: 2.5rem;
                }
            }

            > li:not(:last-child) > .nav-link {
                border-bottom: 0.1rem solid $border-color;
            }
        }

        .navbar-nav .nav-title {
            padding-top: 2rem;
        }

        .navbar-nav .nav-item:last-child {
            .nav-collapse {
                right: 0;
                left: auto;
                border-radius: var(--border-radius-sm) 0 var(--border-radius-sm) var(--border-radius-sm);
            }
        }

        .nav-collapse.show {
            max-height: 80vh;
            overflow-y: auto;
        }
    }

    @include media-breakpoint-up(xl) {
        // Lvl 1
        .nav-lvl--1 > .nav-item {
            margin-right: 2.4rem;
        }
    }
}