.header-principal {
    position: sticky;
    top: 0;
    z-index: $zindex-sticky;
    background-color: var(--white);

    .logos {
        margin-right: var(--header-logo-space-x);
        padding-right: var(--header-logo-space-x);
        border-right: .1rem solid $border-color;

        a {
            display: block;
        }

        img {
            height: 3rem;
            width: auto;

            @include media-breakpoint-up(xl) {
                height: 4.5rem;
            }
        }
    }
}

.header-principal__inner {
    position: relative;
    display: flex;
    align-items: center;
    padding: var(--header-padding);
    min-height: var(--header-min-height);
}

.header-principal-left {
    display: flex;
    align-items: center;
}

.header__title {
    margin-bottom: 0;
    font-size: 1.4rem;
    line-height: 1.14;
    font-family: $font-futura-bold;
    color: var(--body-color-darker);

    @include media-breakpoint-up(xl) {
        font-size: 2rem;
    }
}

.header-principal-right {
    margin-left: auto;
}

.btn.btn-icon--header {
    position: relative;
    color: var(--body-color-darker);

    &:hover,
    &[aria-expanded="true"] {
        color: var(--primary);
    }

    .collapse-wrapper & svg,
    .svg-user {
        font-size: 2rem;
    }

    .title-responsive {
        padding-left: 1.8rem;
    }

    .mail-counter {
        position: absolute;
        top: 1rem;
        left: 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 1.5rem;
        width: 1.5rem;
        font-size: 1.2rem;
        font-weight: $fw-semiBold;
        background-color: var(--brand-2);
        color: $white;
        border-radius: 0.75rem 0 0.75rem 0.75rem;
    }

    @include media-breakpoint-down(md) {
        .nav-right .collapse-wrapper & {
            width: auto;
        }
    }

    @include media-breakpoint-up(lg) {
        .collapse-wrapper & {
            --btn-height: 4.4rem;
        }

        .title-responsive {
            @include sr-only;
        }

        .mail-counter {
            top: 0.8rem;
            left: 2.2rem;
        }
    }
}